export function glcanvas(canvasId, props) {
    const name_1 = "glcanvas";
    const msg = toText(interpolate("Create %P()", [name_1]));
    if (Debug_enabled()) {
        if (Debug_collapsible()) {
            if (Debug_logCollapsed()) {
                console.groupCollapsed(msg);
            }
            else {
                console.group(msg);
            }
        }
        else {
            if (Debug_enabled()) {
                toConsole(interpolate("%P()%P()", [Debug_indent(), msg]));
            }
            if (!Debug_collapsible()) {
                Debug_indent(Debug_indent() + "  ", true);
            }
        }
    }
    console.group(toText(interpolate("Create %P()", [name_1])));
    const res = (() => create(canvasId, props))();
    console.log(some("groupEnd"));
    console.groupEnd();
    if (Debug_collapsible()) {
        console.groupEnd();
    }
    else {
        Debug_indent(substring(Debug_indent(), 2), true);
    }
    return res;
}

