import { printf, toText, substring } from "./.fable/fable-library.3.0.0/String.js";
import { Record, toString } from "./.fable/fable-library.3.0.0/Types.js";
import { record_type, string_type, bool_type, getEnumName } from "./.fable/fable-library.3.0.0/Reflection.js";
import { createAtom } from "./.fable/fable-library.3.0.0/Util.js";

export function throw$(msg) {
    throw (new Error(msg));
}

export function isPowerOf2(x) {
    return (x & (x - 1)) === 0;
}

export function adjustOffset(offset, align) {
    return (~(~Math.ceil(offset / align))) * align;
}

export function boolToInt(b) {
    if (b) {
        return 1;
    }
    else {
        return 0;
    }
}

export const ONE_RADIAN = 180 / 3.141592653589793;

export const ONE_DEGREE = 3.141592653589793 / 180;

export const RAD_PER_DEG = ONE_DEGREE * 1;

export const DEG_PER_RAD = ONE_RADIAN * 1;

export function clipString(count, str) {
    if (str.length > count) {
        return substring(str, 0, count - 3) + "...";
    }
    else {
        return str;
    }
}

export function clipArray(count, arr) {
    return clipString(count, toText(printf("%A"))(arr));
}

export function clipObj(count, o) {
    let copyOfStruct;
    return clipString(count, (copyOfStruct = o, toString(copyOfStruct)));
}

export function enumName(enumType, value) {
    return getEnumName(enumType, value);
}

export class WebglContextAttributes extends Record {
    constructor(alpha, desynchronized, antialias, depth, failIfMajorPerformanceCaveat, powerPreference, premultipliedAlpha, preserveDrawingBuffer, stencil) {
        super();
        this.alpha = alpha;
        this.desynchronized = desynchronized;
        this.antialias = antialias;
        this.depth = depth;
        this.failIfMajorPerformanceCaveat = failIfMajorPerformanceCaveat;
        this.powerPreference = powerPreference;
        this.premultipliedAlpha = premultipliedAlpha;
        this.preserveDrawingBuffer = preserveDrawingBuffer;
        this.stencil = stencil;
    }
}

export function WebglContextAttributes$reflection() {
    return record_type("Utils.WebglContextAttributes", [], WebglContextAttributes, () => [["alpha", bool_type], ["desynchronized", bool_type], ["antialias", bool_type], ["depth", bool_type], ["failIfMajorPerformanceCaveat", bool_type], ["powerPreference", string_type], ["premultipliedAlpha", bool_type], ["preserveDrawingBuffer", bool_type], ["stencil", bool_type]]);
}

export function getClientRect(elem) {
    const cw = elem.clientWidth;
    const ch = elem.clientHeight;
    const rect = elem.getBoundingClientRect();
    rect.x=((rect.x) + ((rect.width - cw) * 0.5));
    rect.y=((rect.y) + ((rect.height - ch) * 0.5));
    rect.width = cw;
    rect.height = ch;
    return rect;
}

export const Debug_enabled = createAtom(false);

export const Debug_collapsible = createAtom(true);

export const Debug_logCollapsed = createAtom(true);

export const Debug_indent = createAtom("");

