export function dirty(data) {
    data.IsDirty = true;
    GlCommon_dirtyScene(data.Scene);
}

function resetViewport(data) {
    const gl = data.Scene.Canvas.Context;
    const vp = Bounds__get_Values(Bounds__Inflate_5E38073B(data.ViewportBounds, -data.BorderWidth));
    const vpy = (gl.canvas.height - vp[1]) - vp[3];
    gl.viewport(vp[0], vpy, vp[2], vp[3]);
}

function clearViewport(data) {
    const gl = data.Scene.Canvas.Context;
    const sb = Bounds__get_Values(data.ViewportBounds);
    const cc = Vec4__get_Values(data.CameraBackground);
    const sby = (gl.canvas.height - sb[1]) - sb[3];
    gl.scissor(sb[0], sby, sb[2], sb[3]);
    gl.clearColor(cc[0], cc[1], cc[2], cc[3]);
    gl.enable(gl.SCISSOR_TEST);
    gl.clear(data.ClearMask);
    gl.disable(gl.SCISSOR_TEST);
}

export function update(data) {
    if (data.IsDirty) {
        const msg = toText(interpolate("Camera %P().update", [data.Name]));
        if (Debug_enabled()) {
            if (Debug_collapsible()) {
                if (Debug_logCollapsed()) {
                    console.groupCollapsed(msg);
                }
                else {
                    console.group(msg);
                }
            }
            else {
                if (Debug_enabled()) {
                    toConsole(interpolate("%P()%P()", [Debug_indent(), msg]));
                }
                if (!Debug_collapsible()) {
                    Debug_indent(Debug_indent() + "  ", true);
                }
            }
        }
        if (data.Kind.tag === 1) {
            throw$(toText(interpolate("Perspective camera update not implemented", [])));
        }
        else {
            update_1(data);
        }
        if (Debug_collapsible()) {
            console.groupEnd();
        }
        else {
            Debug_indent(substring(Debug_indent(), 2), true);
        }
    }
}

export function render(data) {
    const msg = toText(interpolate("Camera %P().render", [data.Name]));
    if (Debug_enabled()) {
        if (Debug_collapsible()) {
            if (Debug_logCollapsed()) {
                console.groupCollapsed(msg);
            }
            else {
                console.group(msg);
            }
        }
        else {
            if (Debug_enabled()) {
                toConsole(interpolate("%P()%P()", [Debug_indent(), msg]));
            }
            if (!Debug_collapsible()) {
                Debug_indent(Debug_indent() + "  ", true);
            }
        }
    }
    update(data);
    if (data.ClearViewport) {
        clearViewport(data);
    }
    resetViewport(data);
    if (data.Kind.tag === 1) {
        throw$(toText(interpolate("Perspective camera rendering not implemented", [])));
    }
    else {
        render_1(data);
    }
    if (Debug_collapsible()) {
        console.groupEnd();
    }
    else {
        Debug_indent(substring(Debug_indent(), 2), true);
    }
}

